import React from 'react'
import Link from 'gatsby-link'
import styled from "styled-components"
import breakpoint from 'styled-components-breakpoint'
import {PhotoWrapper, TextWrapper, PageHeader, SectionHeader, Section} from "../utils/styled-components.js"
import RegistrationBar from '../components/RegistrationBar.js'
import {translate} from 'react-i18next'
import SideMenu from '../components/SideMenu.js'
import i18n from '../utils/i18n.js'


const PhotoWrapperEducation = PhotoWrapper.extend`
  background-image:   linear-gradient(rgba(0,0,0,0.45), rgba(0,0,0,0.45)),
  url('http://res.cloudinary.com/ds0f4qyyt/image/upload/s--ST9NKaaB--/c_scale,q_jpegmini,w_3525/v1493834338/smiles1_falmrt.jpg');
`
const EducationWrapper = styled.div`
  width: 100%
  height: 100%;
`
const TextWrapperEducation = TextWrapper.extend`
  ${props => breakpoint('md', props.theme.breakpoints)`
    margin-left: 10rem;
  `};
`
const MenuWrapper = styled.div`
  margin: 0 auto;
  max-width: 960px;
  display: flex;
`
