const EducationPage =  ({data, t}) => {
  let dataUri = data[t('education.data')].edges;
  return(
    <EducationWrapper>
      <PhotoWrapperEducation/>
      <PageHeader>{t('education.header')}</PageHeader>
      <MenuWrapper id="eduwrapper">
        <SideMenu data={dataUri} />
        <TextWrapperEducation>
            {
              dataUri.map(({node}, i)=>(
                <Section
                id={node.frontmatter.title.split(' ').join('-')}
                key={i}>
                  <SectionHeader>
                {node.frontmatter.title}
                </SectionHeader>
                  <div dangerouslySetInnerHTML={{__html: node.html}}></div>
                </Section>
              ))
            }
        </TextWrapperEducation>
      </MenuWrapper>
      <RegistrationBar/>
    </EducationWrapper>
  )
}


export const query = graphql`
  query EducationQuery {
    en: allMarkdownRemark(
      sort: {fields: [frontmatter___order], order:ASC},
      filter: {fileAbsolutePath: {regex: "/\/home\/bobby\/projects\/Practice\/nslb-gatsby\/src\/data\/education\/en/"}}
    ) {
      edges {
        node {
          frontmatter{
            title
          }
          html
      }
    }
    }
    es: allMarkdownRemark(
      sort: {fields: [frontmatter___order], order:ASC},
      filter: {fileAbsolutePath: {regex: "/\/home\/bobby\/projects\/Practice\/nslb-gatsby\/src\/data\/education\/es/"}}
    ) {
      edges {
        node {
          frontmatter{
            title
          }
          html
      }
    }
  },
}
`
export default translate()(EducationPage)



